import React, { useState, useEffect } from 'react'
import { Link, useHistory } from 'react-router-dom'
import M from 'materialize-css'
import signupImage from "./../../images/2.png"
import "../css/auth.css";

const SignUp = () => {
    const history = useHistory()
    const [name, setName] = useState("")
    const [password, setPasword] = useState("")
    const [email, setEmail] = useState("")
    const [image, setImage] = useState("")
    const [url, setUrl] = useState(undefined)
    useEffect(() => {
        if (url) {
            uploadFields()
        }
    }, [url])
    const uploadPic = () => {
        const data = new FormData()
        data.append("file", image)
        data.append("upload_preset", "recipe_pics")
        data.append("cloud_name", "talkytalky")
        fetch("https://api.cloudinary.com/v1_1/talkytalky/image/upload", {
            method: "post",
            body: data
        })
            .then(res => res.json())
            .then(data => {
                setUrl(data.url)
            })
            .catch(err => {
                console.log(err)
            })
    }
    const uploadFields = () => {
        if (!/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)) {
            M.toast({ html: "invalid email", classes: "#c62828 red darken-3" })
            return
        }
        fetch("/signup", {
            method: "post",
            headers: {
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                name,
                password,
                email,
                pic: url
            })
        }).then(res => res.json())
            .then(data => {
                if (data.error) {
                    M.toast({ html: data.error, classes: "#c62828 red darken-3" })
                }
                else {
                    M.toast({ html: data.message, classes: "#43a047 green darken-1" })
                    history.push('/signin')
                }
            }).catch(err => {
                console.log(err)
            })
    }
    const PostData = () => {
        if (image) {
            uploadPic()
        } else {
            uploadFields()
        }

    }

    return (
        <div className="login-screen">
            <div>
                <img src={signupImage} className="signup-image"></img>
            </div>
            <div className="auth-card">
                <div className=" input-field">
                    <h2 style={{ fontFamily: "sans-serif" }}>MyRecipe Diary</h2>
                    <input
                        type="text"
                        placeholder="name"
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        style = {{margin:"30px auto"}}
                    />
                    <input
                        type="text"
                        placeholder="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                    />
                    <input
                        type="password"
                        placeholder="password"
                        value={password}
                        onChange={(e) => setPasword(e.target.value)}
                    />
                    <div className="file-field input-field">
                        <div className="btn btn-warning #64b5f6 darken-1">
                            <span style={{ color: "#000" }}>Upload Pic</span>
                            <input type="file" onChange={(e) => setImage(e.target.files[0])} />
                        </div>
                        <div className="file-path-wrapper">
                            <input className="file-path validate" type="text" />
                        </div>
                    </div>
                    <button className="btn btn-success waves-effect waves-light #64b5f6 darken-1"
                        onClick={() => PostData()}
                        style={{ marginBottom: "20px" }}
                    >
                        <span style={{ color: "#fff" }}>SignUp</span>
                    </button>
                    <h5>
                        <Link to="/signin" className="links-text">Already have an account ?</Link>
                    </h5>
                </div>
            </div>
        </div>
    )
}


export default SignUp